import React, {Component} from 'react';
import './App.css';
import ProvinceTable from "./components/ProvinceTable";
import DistrictTable from "./components/DistrictTable";

class App extends Component {

    constructor(props) {
        super(props);
        this.state = {
            showMode: "province",
            provinceId: '',
            provinceName: ''
        };
        this._handleShowProvinces = this._handleShowProvinces.bind(this);
        this._handleShowDistricts = this._handleShowDistricts.bind(this);
    };

    _handleShowProvinces() {
        this.setState({
            provinceId: '',
            showMode: "province"
        });
    };

    _handleShowDistricts(provinceId, provinceName) {
        this.setState({
            provinceId,
            provinceName,
            showMode: "district"
        });
    };

    _renderComponent(showMode) {
        const {provinceId, provinceName} = this.state;
        return {
            "province": <ProvinceTable onShowDistricts={this._handleShowDistricts} />,
            "district": <DistrictTable provinceId={provinceId} provinceName={provinceName} onBack={this._handleShowProvinces} />
        }[showMode] || null;
    };

    render() {
        const {showMode} = this.state;
        return (
            <div className="container">
                {this._renderComponent(showMode)}
            </div>
        );
    }
}

export default App;
