import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {
    getDistricts,
    updateDistrict,
    createDistrict,
    deleteDistrict
} from '../apis/DistrictApi';
import {filter} from '../utils/filter';
import './DistrictTable.css';

const emptyEditingData = {id: '', name: ''};

class DistrictTable extends Component {

    constructor(props) {
        super(props);
        this.state = {
            districts: [],
            searchKey: '',
            editing: false,
            editingId: '',
            editingData: emptyEditingData,
        };
        this._handleEditDistrict = this._handleEditDistrict.bind(this);
        this._handleInputChange = this._handleInputChange.bind(this);
        this._handleCancelClick = this._handleCancelClick.bind(this);
        this._handleUpdateClick = this._handleUpdateClick.bind(this);
        this._handleDeleteClick = this._handleDeleteClick.bind(this);
        this._handleCreateDistrictClick = this._handleCreateDistrictClick.bind(this);
        this._handleBackClick = this._handleBackClick.bind(this);
        this._handleSearchChange = this._handleSearchChange.bind(this);
    };

    async componentDidMount() {
        try {
            const districts = await getDistricts(this.props.provinceId || '');
            this.setState({districts});
        } catch (e) {
            console.error(e);
            alert(e);
        }
    };

    _handleEditDistrict(id) {
        return () => {
            const districtInEdit = this.state.districts.find(d => d.id === id);
            this.setState({
                editing: true,
                editingId: id,
                editingData: districtInEdit,
            })
        };
    };

    _handleInputChange(e, fieldName) {
        const value = e.target.value;
        this.setState((prevState) => {
            const {editingData} = prevState;
            const newEditingData = {...editingData};
            newEditingData[fieldName] = value;
            return {...prevState, editingData: newEditingData};
        });
    };

    _handleCancelClick() {
        this._resetForm();
    };

    _handleUpdateClick() {
        const {editingId, editingData, districts} = this.state;
        updateDistrict(editingId, editingData).then(
            (data) => {
                const i = districts.findIndex(p => p.id === editingId);
                if (i >= 0) {
                    districts.splice(i, 1, data);
                    this.setState({districts});
                    this._resetForm();
                }
            },
            (err) => {
                console.error(err);
                alert(err);
            }
        );
    };

    _handleDeleteClick(id) {
        return () => {
            const {districts} = this.state;
            deleteDistrict(id).then(
                () => {
                    const i = districts.findIndex(d => d.id === id);
                    if (i >= 0) {
                        districts.splice(i, 1);
                        this.setState({districts});
                        this._resetForm();
                    }
                },
                (err) => {
                    console.error(err);
                    alert(err);
                }
            );
        };
    };

    _handleCreateDistrictClick() {
        const provinceId = this.props.provinceId || '';
        const {editingData, districts} = this.state;
        createDistrict(provinceId, editingData).then(
            (data) => {
                districts.push(data);
                this.setState({districts});
                this._resetForm();
            },
            (err) => {
                console.error(err);
                alert(err);
            }
        );
    };

    _handleBackClick() {
        this.props.onBack();
    };

    _handleSearchChange(e) {
        const searchKey = e.target.value;
        this.setState({searchKey});
    };

    _resetForm() {
        this.setState({
            editing: false,
            editingId: '',
            editingData: emptyEditingData,
        });
    }

    render() {
        const {searchKey, editing, editingData} =  this.state;
        const districts = filter(searchKey, this.state.districts, ["name"]);
        return (
            <React.Fragment>
                <h2>Danh sách Quận/Huyện của {this.props.provinceName || ''}</h2>
                <div className="district-control">
                    <div className="control-item">
                        <button onClick={this._handleBackClick}>Trở lại danh sách Tỉnh</button>
                    </div>
                </div>
                <div className="district-control">
                    <div className="control-item">
                        <input
                            type="text"
                            placeholder="Tìm kiếm theo tên huyện"
                            value={searchKey}
                            onChange={this._handleSearchChange}
                        />
                    </div>
                    <div className="control-item">
                        <label htmlFor="district-id-input">ID Huyện:&nbsp;</label>
                        <input
                            id="district-id-input"
                            type="text"
                            value={editingData.id}
                            onChange={(e) => {this._handleInputChange(e, "id")}}
                        />
                    </div>
                    <div className="control-item">
                        <label htmlFor="district-name-input">Tên Huyện:&nbsp;</label>
                        <input
                            id="district-name-input"
                            type="text"
                            value={editingData.name}
                            onChange={(e) => {this._handleInputChange(e, "name")}}
                        />
                    </div>
                    {
                        editing
                        ? <div>
                            <button onClick={this._handleUpdateClick}>Cập nhật</button>&nbsp;
                            <button onClick={this._handleCancelClick}>Quay lại</button>
                        </div>
                        : <button onClick={this._handleCreateDistrictClick}>Thêm</button>
                    }
                </div>
                <table>
                    <thead>
                    <tr>
                        <th>STT</th>
                        <th>ID</th>
                        <th>Tên</th>
                        <th>Hành động</th>
                    </tr>
                    </thead>
                    <tbody>
                    {
                        districts.map((district, i) => (
                            <tr key={district.id}>
                                <td>{i + 1}</td>
                                <td>{district.id}</td>
                                <td>{district.name}</td>
                                <td>
                                    <button
                                        onClick={this._handleEditDistrict(district.id)}
                                    >Sửa</button>&nbsp;
                                    <button
                                        onClick={this._handleDeleteClick(district.id)}
                                    >Xoá</button>
                                </td>
                            </tr>
                        ))
                    }
                    </tbody>
                </table>
            </React.Fragment>
        );
    }
}

DistrictTable.propTypes = {
    provinceId: PropTypes.string,
    provinceName: PropTypes.string,
    onBack: PropTypes.func,
};

export default DistrictTable;
