import {
    getWithoutAuth,
    postWithoutAuth,
    putWithoutAuth,
    deleteWithoutAuth
} from './Sender';

export function getProvinces() {
    const route = '/apis/province_api.php';
    return getWithoutAuth(route);
};

export function createProvince(payload) {
    const route = `/apis/province_api.php`;
    return postWithoutAuth(route, payload);
};

export function updateProvince(id, payload) {
    const route = `/apis/province_api.php?id=${id}`;
    return putWithoutAuth(route, payload);
};

export function deleteProvince(id) {
    const route = `/apis/province_api.php?id=${id}`;
    return deleteWithoutAuth(route);
};