export function filter(key, data, propertyPath = []) {
    if (key && key.length > 0) {
        try {
            let keyRegExp = new RegExp(key, 'i');
            return data.filter((element) => {
                if (propertyPath.length > 0) {
                    const objPathReducer = (obj, prop) => obj[prop];
                    return keyRegExp.test(propertyPath.reduce(objPathReducer, element));
                } else {
                    return keyRegExp.test(element);
                }
            });
        } catch (e) {
            return data;
        }
    } else {
        return data;
    }
};