import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {
    getProvinces,
    updateProvince,
    createProvince,
    deleteProvince
} from '../apis/ProvinceApi';
import {filter} from '../utils/filter';
import './ProvinceTable.css';

const emptyEditingData = {id: '', name: ''};

class ProvinceTable extends Component {

    constructor(props) {
        super(props);
        this.state = {
            provinces: [],
            searchKey: '',
            editing: false,
            editingId: '',
            editingData: emptyEditingData,
        };
        this._handleEditProvince = this._handleEditProvince.bind(this);
        this._handleInputChange = this._handleInputChange.bind(this);
        this._handleCancelClick = this._handleCancelClick.bind(this);
        this._handleUpdateClick = this._handleUpdateClick.bind(this);
        this._handleDeleteClick = this._handleDeleteClick.bind(this);
        this._handleCreateProvinceClick = this._handleCreateProvinceClick.bind(this);
        this._handleShowDistricts = this._handleShowDistricts.bind(this);
        this._handleSearchChange = this._handleSearchChange.bind(this);
    };

    async componentDidMount() {
        try {
            const provinces = await getProvinces();
            this.setState({provinces});
        } catch (e) {
            console.error(e);
            alert(e);
        }
    };

    _handleEditProvince(id) {
        return () => {
            const provinceInEdit = this.state.provinces.find(p => p.id === id);
            this.setState({
                editing: true,
                editingId: id,
                editingData: provinceInEdit,
            })
        };
    };

    _handleInputChange(e, fieldName) {
        const value = e.target.value;
        this.setState((prevState) => {
            const {editingData} = prevState;
            const newEditingData = {...editingData};
            newEditingData[fieldName] = value;
            return {...prevState, editingData: newEditingData};
        });
    };

    _handleCancelClick() {
        this._resetForm();
    };

    _handleUpdateClick() {
        const {editingId, editingData, provinces} = this.state;
        updateProvince(editingId, editingData).then(
            (data) => {
                const i = provinces.findIndex(p => p.id === editingId);
                if (i >= 0) {
                    provinces.splice(i, 1, data);
                    this.setState({provinces});
                    this._resetForm();
                }
            },
            (err) => {
                console.error(err);
                alert(err);
            }
        );
    };

    _handleDeleteClick(id) {
        return () => {
            const {provinces} = this.state;
            deleteProvince(id).then(
                () => {
                    const i = provinces.findIndex(p => p.id === id);
                    if (i >= 0) {
                        provinces.splice(i, 1);
                        this.setState({provinces});
                        this._resetForm();
                    }
                },
                (err) => {
                    console.error(err);
                    alert(err);
                }
            );
        };
    };

    _handleCreateProvinceClick() {
        const {editingData, provinces} = this.state;
        createProvince(editingData).then(
            (data) => {
                provinces.push(data);
                this.setState({provinces});
                this._resetForm();
            },
            (err) => {
                console.error(err);
                alert(err);
            }
        );
    };

    _handleShowDistricts(provinceId, provinceName) {
        return () => {
            this.props.onShowDistricts(provinceId, provinceName);
        };
    };

    _handleSearchChange(e) {
        const searchKey = e.target.value;
        this.setState({searchKey});
    };

    _resetForm() {
        this.setState({
            editing: false,
            editingId: '',
            editingData: emptyEditingData,
        });
    }

    render() {
        const {searchKey, editing, editingData} =  this.state;
        const provinces = filter(searchKey, this.state.provinces, ["name"]);
        return (
            <React.Fragment>
                <h2>Danh sách Tỉnh/Thành phố</h2>
                <div className="province-control">
                    <div className="control-item">
                        <input
                            type="text"
                            placeholder="Tìm kiếm theo tên tỉnh"
                            value={searchKey}
                            onChange={this._handleSearchChange}
                        />
                    </div>
                    <div className="control-item">
                        <label htmlFor="province-id-input">ID Tỉnh:&nbsp;</label>
                        <input
                            id="province-id-input"
                            type="text"
                            value={editingData.id}
                            onChange={(e) => {this._handleInputChange(e, "id")}}
                        />
                    </div>
                    <div className="control-item">
                        <label htmlFor="province-name-input">Tên Tỉnh:&nbsp;</label>
                        <input
                            id="province-name-input"
                            type="text"
                            value={editingData.name}
                            onChange={(e) => {this._handleInputChange(e, "name")}}
                        />
                    </div>
                    {
                        editing
                        ? <div>
                            <button onClick={this._handleUpdateClick}>Cập nhật</button>&nbsp;
                            <button onClick={this._handleCancelClick}>Quay lại</button>
                        </div>
                        : <button onClick={this._handleCreateProvinceClick}>Thêm</button>
                    }
                </div>
                <table>
                    <thead>
                        <tr>
                            <th>STT</th>
                            <th>ID</th>
                            <th>Tên</th>
                            <th>Số lượng huyện</th>
                            <th>Hành động</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            provinces.map((province, i) => (
                                <tr key={province.id}>
                                    <td>{i + 1}</td>
                                    <td>{province.id}</td>
                                    <td>{province.name}</td>
                                    <td>{province.districts}</td>
                                    <td>
                                        <button
                                            onClick={this._handleShowDistricts(province.id, province.name)}
                                        >DS Huyện</button>&nbsp;
                                        <button
                                            onClick={this._handleEditProvince(province.id)}
                                        >Sửa</button>&nbsp;
                                        <button
                                            onClick={this._handleDeleteClick(province.id)}
                                        >Xoá</button>
                                    </td>
                                </tr>
                            ))
                        }
                    </tbody>
                </table>
            </React.Fragment>
        );
    }
}

ProvinceTable.propTypes = {
    onShowDistricts: PropTypes.func,
};

export default ProvinceTable;
