import axios from 'axios';
const BASE_URL = 'http://localhost:80';

function handleResponse(response) {
    return Promise.resolve(response.data.data);
};

function handleError(error) {
    return Promise.reject(error.message);
};

export function getWithoutAuth(route) {
    return axios.get(`${BASE_URL}${route}`).then(handleResponse, handleError);
};

export function postWithoutAuth(route, payload) {
    return axios.post(`${BASE_URL}${route}`, payload).then(handleResponse, handleError);
};

export function putWithoutAuth(route, payload) {
    return axios.put(`${BASE_URL}${route}`, payload).then(handleResponse, handleError);
};

export function deleteWithoutAuth(route) {
    return axios.delete(`${BASE_URL}${route}`).then(handleResponse, handleError);
};

