import {
    getWithoutAuth,
    postWithoutAuth,
    putWithoutAuth,
    deleteWithoutAuth
} from './Sender';

export function getDistricts(provinceId) {
    const route = `/apis/district_api.php?province_id=${provinceId}`;
    return getWithoutAuth(route);
};

export function getDistrict(id) {
    const route = `/apis/district_api.php?id=${id}`;
    return getWithoutAuth(route);
};

export function createDistrict(provinceId, payload) {
    const route = `/apis/district_api.php?province_id=${provinceId}`;
    return postWithoutAuth(route, payload);
};

export function updateDistrict(id, payload) {
    const route = `/apis/district_api.php?id=${id}`;
    return putWithoutAuth(route, payload);
};

export function deleteDistrict(id) {
    const route = `/apis/district_api.php?id=${id}`;
    return deleteWithoutAuth(route);
};